import { Chart } from "chart.js";
import { useEffect, useState } from "react";
import "./CheckBox.scss";

let myChart;

export const CheckBoxs = (props) => {
  const { END_POINT, API_KEY } = props;

  const [prefs, setPrefs] = useState([]);
  const [populations, setPopulations] = useState({});

  //
  const setHeader = (key) => {
    const headers = new Headers();
    headers.set("X-API-KEY", key);
    return headers;
  };

  //
  const showGraph = (code, prefName) => {
    const headers = setHeader(API_KEY);
    const URL = `${END_POINT}/api/v1/population/composition/perYear?prefCode=${code}`;

    fetch(URL, { headers: headers })
      .then((res) => {
        return res.json();
      })
      .then((json) => {
        const thePref = json.result.data[0].data;
        let newPopulations;

        //
        if (code in populations) {
          //
          newPopulations = Object.assign(populations);
          delete newPopulations[code.toString()];
        } else {
          //
          newPopulations = Object.assign(populations);
          newPopulations[code.toString()] = { pref: prefName, data: thePref };
        }

        const graph = document.querySelector("#drawGraph").getContext("2d");

        try {
          myChart.destroy();
        } catch {}

        const showGraphData = [];

        //chart.js
        for (const one in newPopulations) {
          const onePrefData = newPopulations[one];
          const onePrefValueData = onePrefData.data.map((name) => {
            return name.value;
          });
          showGraphData.push({
            label: onePrefData.pref,
            type: "line",
            fill: false,
            data: onePrefValueData,
            borderColor: `rgb(${Math.floor(Math.random() * 256)}, ${Math.floor(
              Math.random() * 256
            )}, ${Math.floor(Math.random() * 256)})`,
            yAxisID: "y-axis-1",
          });
        }

        myChart = new Chart(graph, {
          type: "line",
          data: {
            labels: thePref.map((name) => {
              return name.year;
            }),
            datasets: showGraphData,
          },
          options: {
            scales: {
              yAxes: [
                {
                  id: "y-axis-1",
                  type: "linear",
                  position: "left",
                  scaleLabel: {
                    display: window.screen.width > 500,
                    labelString: "",
                  },
                  ticks: {
                    min: 0,
                  },
                },
              ],
              xAxes: [
                {
                  scaleLabel: {
                    display: window.screen.width > 500,
                    labelString: "",
                  },
                  ticks: {
                    min: 0,
                  },
                },
              ],
            },
            maintainAspectRatio: false,
          },
        });

        setPopulations(newPopulations);
      });
  };

  //
  useEffect(() => {
    const headers = setHeader(API_KEY);
    const URL = `${END_POINT}/api/v1/prefectures`;

    fetch(URL, { headers: headers })
      .then((res) => {
        return res.json();
      })
      .then((json) => {
        setPrefs(json.result);
      });

    //Chart.jsURLlabels
    const URL2 = `${END_POINT}/api/v1/population/composition/perYear?prefCode=1`;
    fetch(URL2, { headers: headers })
      .then((res) => {
        return res.json();
      })
      .then((json) => {
        const thePref = json.result.data[0].data;
        const graph = document.querySelector("#drawGraph").getContext("2d");

        try {
          myChart.destroy();
        } catch {}

        myChart = new Chart(graph, {
          type: "line",
          data: {
            labels: thePref.map((name) => {
              return name.year;
            }),
          },
          options: {
            scales: {
              yAxes: [
                {
                  id: "y-axis-1",
                  type: "linear",
                  position: "left",
                  scaleLabel: {
                    display: window.screen.width > 500,
                    labelString: "",
                  },
                  ticks: {
                    min: 0,
                  },
                },
              ],
              xAxes: [
                {
                  scaleLabel: {
                    display: window.screen.width > 500,
                    labelString: "",
                  },
                  ticks: {
                    min: 0,
                  },
                },
              ],
            },
            maintainAspectRatio: window.screen.width > 500,
          },
        });
      });
  }, [API_KEY, END_POINT]);

  return (
    <>
      <div className="container">
        <h2 className="prefViewTitle"></h2>
        <hr />
        <div className="checkBoxes">
          {prefs.map((name, index) => {
            return (
              <div className="one" key={name.prefCode.toString()}>
                <input
                  type="checkbox"
                  id={name.prefCode.toString()}
                  onChange={() => showGraph(name.prefCode, name.prefName)}
                  className="oneCheckbox"
                />
                <label htmlFor={name.prefCode.toString()} className="oneLabel">
                  {name.prefName}
                </label>
              </div>
            );
          })}
        </div>
        <div className="graphCanvasContainer">
          <canvas className="populationGraph" id="drawGraph" />
        </div>
      </div>
    </>
  );
};
